import { Component, Fragment } from "react/cjs/react.production.min";

export const Search = ({Searchcity}) =>{

        return(
            <Fragment>
                <div className='Search-Container'> 
                    <div className='Input-container'>
                        <input type="search" className="form-control rounded" placeholder="Enter City Name" aria-label="Search"
                            aria-describedby="search-addon" />
                        <button className='btn btn-primary' onClick={Searchcity}>Search</button>
                    </div>
                <hr></hr>
            </div>
            </Fragment>
        );
};
