import './App.css';
import { Component } from 'react/cjs/react.production.min';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import '../node_modules/font-awesome/css/font-awesome.min.css'; 
import {Search} from './Search.js'
import Weather from './Weather';
import Scroll from './Scroll';
import _ from 'lodash';
class App extends Component{
 
  constructor(props){
    super(props);
    
    this.state={
      weather :{},
      Datareceived : false,
      url:'',
      city:'',
      flag : 0,
      time :[],
      temperature : [],
      clouds : [],
      icon : [],
      Fulldata :[],
      counter : 0,
      counter1:0,
      counter2:0,
      extract:[]
    }
    
  }
  searchcity=(event)=>{
    this.setState({city:event.target.parentNode.firstChild.value, counter: 0})
  }
  tConvert = (time)=>{
    time = time.toString ().match (/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
  
    if (time.length > 1) { 
      
      time = time.slice (1); 
      time[1] = +time[0] < 12 ? 'AM' : 'PM'; 
     
      time[0] = +time[0] % 12 || 12; 
      time[2]='';
      time[3]=''
    }
    return time.join(''); 
  }
getDayOfWeek=(date)=>{
    const dayOfWeek = new Date(date).getDay();    
    return isNaN(dayOfWeek) ? null : 
      ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'][dayOfWeek];
  }
findTempCountofToday=()=>{

    var count=this.state.extract.filter(i=> i[0]===this.getDayOfWeek(new Date())).length;
    return count;
}
componentDidUpdate(pp,ps,cs) {

      if (ps.city !== this.state.city){

            const url ='https://api.openweathermap.org/data/2.5/forecast?q='+this.state.city+'&appid=d36710684a86e668c449c2ea633bf286&units=metric';
            fetch(url)
            .then((response) => {
              if (response.ok) {
                return response.json();
              } else {
                throw new Error('Something went wrong');
              }
            })
            .then((json) => {
                this.setState({
                    Datareceived : true,
                    flag : 1,
                    Fulldata : json.list
                }
                );
                var extract=this.state.Fulldata.map(item =>[['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'][new Date(item.dt_txt).getDay()]
                ,item.dt_txt.split(' ')[0]
            ,this.tConvert(item.dt_txt.split(' ')[1]),
            ,item.main.temp,item.weather[0].icon,item.weather[0].description]);
            this.setState({extract:extract});
            this.setState({counter1:this.findTempCountofToday(),
            counter2:this.findTempCountofToday()+8})

            })
            .catch((error) => {
              console.log(error)
              this.setState({Datareceived : false, flag : 1})
            });
     
}}

render(){
   
    if(this.state.Datareceived && this.state.flag===1){
      
      return (
        <div className="App">
          <h1>Weather forecasting</h1><h6 id='note'>(3days/3hour)</h6>
          <Search Searchcity={this.searchcity}/>
          <div className='Search-Container'>
                <h2>{this.state.city}</h2><hr className='hru'></hr>
          </div>

             <Weather Fulldata={this.state.Fulldata}  city={this.state.city} Counter1={this.state.counter1} Counter2={this.state.counter2} counter={this.state.counter} extract={this.state.extract}/>
                
        </div>
        );
    }
    else if (!this.state.Datareceived && this.state.flag===0){
      return (
        <div className="App">
          <h1>Weather forecasting</h1><h6 id='note'>(3days/3hour)</h6>
          <Search Searchcity={this.searchcity}/>
        </div>
        );

    }
    else if (!this.state.Datareceived && this.state.flag===1){
      
      return (
        <div className="App">
          <h1>Weather forecasting</h1><h6 id='note'>(3days/3hour)</h6>
          <Search Searchcity={this.searchcity} Clearchilditem={this.Clearchilditem}/>
          <div className='Search-Container'>
                <h2>No matches found</h2><hr className='hru'></hr>
          </div>
        </div>
        );

    }

  }

}
  


export default App;
