import react, { Fragment } from "react";
import { Component } from "react/cjs/react.production.min";

class Weather extends Component{
    constructor(props){
        super(props);
        this.state = {
            city : this.props.city,
            temp : this.props.Fulldata[0].main.temp,
            icon : this.props.Fulldata[0].weather[0].icon,
            cloud : this.props.Fulldata[0].weather[0].description,
            time : this.props.Fulldata[0].dt_txt,
            temp1:'',
            icon1:'01d',
            cloud1:'',
            time1:'',
            temp2:'',
            icon2:'01d',
            cloud2:'',
            time2:'',
            counter : 0,
            extract : [],
            counter1:0,
            counter2:0,
            day:''
        };
                
    }
    getDayOfWeek=(date)=>{
        const dayOfWeek = new Date(date).getDay();    
        return isNaN(dayOfWeek) ? null : 
          ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'][dayOfWeek];
      }
    findTempCountofToday=()=>{
        
        var count=this.props.extract.filter(i=> i[0]===this.getDayOfWeek(new Date(this.props.Fulldata[0].dt_txt))).length;
        return count;
    }
    tConvert = (time)=>{
        time = time.toString().match (/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
        if (time.length > 1) { 
          
          time = time.slice (1);  
          time[1] = +time[0] < 12 ? 'AM' : 'PM'; 
         
          time[0] = +time[0] % 12 || 12; 
          time[2]='';
          time[3]=''
        }
        return time.join(''); 
      }

    componentDidMount() {
        this.timerID = setInterval(
          () => this.tick(),
          2000
        );

    }
    
    tick() {
        if (this.state.counter === this.findTempCountofToday() || this.state.city!==this.props.city ){
           
            this.setState({counter : 0})
        }
        if (this.state.counter1 === this.findTempCountofToday()+8 || this.state.city!==this.props.city ){
           
            this.setState({counter1 : this.findTempCountofToday()})
        }
        if (this.state.counter2 === this.findTempCountofToday()+16 || this.state.city!==this.props.city ){
           
            this.setState({counter2 : (this.findTempCountofToday()+8)})
        }
        if(this.state.counter1===0 || this.state.counter2===0)
        {
            this.setState({counter1:this.props.Counter1,counter2:this.props.Counter2})
        }
        if(this.state.counter < this.findTempCountofToday())
        {
            this.setState({
                temp : this.props.Fulldata[this.state.counter].main.temp,
                icon : this.props.Fulldata[this.state.counter].weather[0].icon,
                cloud : this.props.Fulldata[this.state.counter].weather[0].description,
                time : this.props.Fulldata[this.state.counter].dt_txt,
                counter : this.state.counter+1,
                city: this.props.city  
                });
            
        } if(this.state.counter1 >=this.findTempCountofToday() && this.state.counter1 < this.findTempCountofToday()+8)
        {
           
            this.setState({

                temp1 : this.props.Fulldata[this.state.counter1].main.temp,
                icon1 : this.props.Fulldata[this.state.counter1].weather[0].icon,
                cloud1 : this.props.Fulldata[this.state.counter1].weather[0].description,
                time1 : this.props.extract[this.state.counter1][2]+', '+this.props.extract[this.state.counter1][1],
                counter1 : this.state.counter1+1,
                city: this.props.city  
                });
        } if (this.state.counter2 >= this.findTempCountofToday()+8 && this.state.counter2 < this.findTempCountofToday()+16)
        {
           
            this.setState({
                temp2 : this.props.Fulldata[this.state.counter2].main.temp,
                icon2 : this.props.Fulldata[this.state.counter2].weather[0].icon,
                cloud2 : this.props.Fulldata[this.state.counter2].weather[0].description,
                time2 : this.props.extract[this.state.counter2][2]+', '+this.props.extract[this.state.counter2][1],
                counter2 : this.state.counter2+1,
                city: this.props.city,
                day : this.props.extract[this.state.counter2][0]
                });
        }

        
      }
        componentWillUnmount() {
         clearInterval(this.timerID);
        this.setState({counter : 0, counter1 : this.findTempCountofToday(), counter2 : this.findTempCountofToday()+8})
      }
    render(){
                  return(
            <Fragment>
                <div className='Weather-Container'>
                <div className='Image-Container'>
                    <img src={"https://openweathermap.org/img/wn/"+this.state.icon+"@2x.png"} alt=""></img>
                    <h2>{Math.round(this.state.temp)}c</h2>
                    </div>
                    <div className='Details-Container'>
                    <h2>Today</h2>   
                    <h6>{this.tConvert(this.state.time.split(' ')[1])}, {this.state.time.split(' ')[0]}</h6>
                    <h5>{this.state.cloud}</h5>
                </div>
                </div>
                <div className='Weather-Container'>
                <div className='Image-Container'>
                    <img src={"https://openweathermap.org/img/wn/"+this.state.icon1+"@2x.png" }alt=""></img>
                    <h2>{Math.round(this.state.temp1)}c</h2>
                    </div>
                    <div className='Details-Container'>
                    <h2>Tomorrow</h2>   
                    <h6>{this.state.time1}</h6>
                    <h5>{this.state.cloud1}</h5>
                </div>
                </div>
                <div className='Weather-Container'>
                <div className='Image-Container'>
                    <img src={"https://openweathermap.org/img/wn/"+this.state.icon2+"@2x.png" }alt=""></img>
                    <h2>{Math.round(this.state.temp2)}c</h2>
                    </div>
                    <div className='Details-Container'>
                    <h2>{this.state.day}</h2>   
                    <h6>{this.state.time2}</h6>
                    <h5>{this.state.cloud2}</h5>
                </div>
                </div>
            </Fragment>
       );
    }
}
export default Weather;